.RoundedBtn {
    position: fixed;
    background-color: lime;
    border-radius: 20%;
    bottom: 50px;
    right: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    cursor: pointer;
}
.RoundedBtn:hover {
    box-shadow: 2px 2px 12px deeppink;
}